<template>
  <div class="mem_package" >
    
    <br>
    <!--  -->
    <el-form :inline="true" :model="formInline" class="demo-form-inline">

      <el-form-item label="">
        <el-input  placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-input  placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-input  ></el-input>
      </el-form-item>

      <el-form-item >
        <template scope="scope">
        <el-button type="success" class="el-icon-search"@click="onSubmit"></el-button>
        <el-button type="success" @click="add_package(scope.$index,scope.row)"></el-button>
        </template>
      </el-form-item>

    </el-form>

    <!--  -->
    <div class="content">
      <el-table  :data="tableData"  style="width: 100%" >
        <el-table-column  prop="username" label=""  width="180"></el-table-column>
        <el-table-column  prop="buy_package" label=""  width="180"></el-table-column>
        <el-table-column  prop="date" label=""  width="180"></el-table-column>
        <el-table-column  prop="avaliable_date" label=""  width="180"></el-table-column>
        <el-table-column label="">
          <template scope="scope">
            <el-button size="small" class="font-color" ><i class="fa fa-book" aria-hidden="true"></i></el-button>
          </template>
        </el-table-column>
          </el-table>
      </el-table>    
  </div>
  
  <!--  -->
  <el-dialog title="" v-model="dialogFormVisible" size="tiny" >
    <el-form ref="form" :model="form" label-width="120px">

    <el-form-item label="">
      <el-input v-model="form.username"  ></el-input>
    </el-form-item>

    <el-form-item label="">
      <el-input v-model="form.account"  ></el-input>
    </el-form-item>

    <el-form-item label="">
    <el-select v-model="value3" placeholder="">
      <el-option label="" value=""></el-option>
      <el-option label="" value=""></el-option>
    </el-select>
    </el-form-item>

    <el-form-item label="">
      <el-date-picker
        v-model="value1"
        type="date"
        placeholder=""
        :picker-options="pickerOptions0">
      </el-date-picker>
    </el-form-item>

    <el-form-item label="">
      <el-input v-model="form.buy_package" placeholder=""></el-input>
    </el-form-item> 

    <el-form-item label="">
      <el-select v-model="value2" placeholder="">
        <el-option label="" value=""></el-option>
        <el-option label="" value=""></el-option>
        <el-option label="" value=""></el-option>
      </el-select>
    </el-form-item>
    
    <el-form-item class="center">
    <template scope="scope">
      <el-button type="primary" @click="handleSaveadd()" :loading="editLoading"></el-button>
      <el-button  @click="dialogFormVisible = false"></el-button>
    </template>
    </el-form-item>

    </el-form> 
  </el-dialog> 
  
  <!--  -->
  <div class="block">
     <el-pagination
       @size-change="handleSizeChange"
       @current-change="handleCurrentChange"
       :current-page="currentPage1"
       :page-sizes="[10, 20, 30, 40]"
       :page-size="100"
       layout="total, sizes, prev, pager, next, jumper"
       :total="tableData.length">
     </el-pagination>
   </div>
   </div>
   </div>
</template>
<script >
  import {api} from '../../global/api';
export default {
  name: 'mem_package',
  data () {
    return {
     
      dialogFormVisible:false,
      editLoading:false,//
      currentPage1: 5,
      activeIndex: '1',
      value3:'',
      value1:'',
      value2:'',
      formInline: {
        username:'',
        buy_package:'',
        date:'',
        avaliable_date:'',
        account:'',
        style:'',
      },
      form : {
        username:'',
        buy_package:'',
        date:'',
        avaliable_date:'',
        account:'',
        style:'',
      },
      tableData: [{
                 username:'',
                 account:'',
                 buy_package:'',
                 date:'',
                 avaliable_date:'',
               }]
    }
  },
  //
  mounted:function(){
    this.$http.get(api.style).then(function(response){
      this.tableData=response.data.tableData;
    });
  },
  methods: {
    onSubmit() {
      this.$message('~');
    },
    //
    add_package (index, row) {
      this.dialogFormVisible = true;
      this.formInline = Object.assign({}, row);
      this.table_index = index;
    },
    //
    handleSaveadd () {

      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        cancelButtonClass: 'cancel'
      }).then(() => {
      //
       console.log(this.value1);
       let year= new Date(this.value1).getFullYear(),
           month=new Date(this.value1).getMonth()+1,
           day=new Date(this.value1).getDate();
       let dateformat=year+'-'+month+'-'+day;
       this.form.date=dateformat;
    
       this.tableData.push(this.form);
       this.$message({
          message: "",
          type: 'success'
       });
       this.dialogFormVisible = false;
       //
       this.value1='';
       this.value2='';
       this.value3='';
       this.form={
         username:'',
         buy_package:'',
         date:'',
         avaliable_date:'',
         account:'',
         style:'',
       }
      });
    },

    handleSizeChange(val) {
     console.log(` ${val} `);
    },

    handleCurrentChange(val) {
     console.log(`: ${val}`);
    },
    handleSelect(key, keyPath) {
     console.log(key, keyPath);
    },
    pickerOptions0: {
        disabledDate(time) {
          return time.getTime() < Date.now() - 8.64e7;
        }
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >
  .mem_package{
    padding: 20px;
  }
  .mem_package .content{
    height: 400px;
    background: white;
  }
  .mem_package .block{
    text-align: center;
  }
  .mem_package .center{
    text-align: center;
  }
  .mem_package .font-color{
    color: #50bfff; 
     border:0px solid #50bfff;
  }
  .mem_package .left{
    float: left; 
    text-align: center;
    width: 50%;
  }
  .mem_package .right{
    float: right; 
    text-align: center;
    width: 50%;
  }
  .mem_package .center{
    text-align: center;
  }

</style>



