<template>
  <div class="package">
  <!-- <br> -->
  <el-form :inline="true" :model="formInline" class="demo-form-inline"> 
    <el-form-item label="">
      <el-select v-model="formInline.region" placeholder="">
        <el-option label="A" value="A"></el-option>
        <el-option label="B" value="B"></el-option>
        <el-option label="C" value="C"></el-option>
        <el-option label="D" value="D"></el-option>
        <el-option label="E" value="E"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="">
      <el-input v-model="formInline.user" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="">
      <el-select v-model="formInline.state" placeholder="">
        <el-option label="" value="ture"></el-option>
        <el-option label="" value="false"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item>
      <el-button type="success" class="el-icon-search"@click="onSubmit"></el-button>
    </el-form-item>
    <el-button class="filter-item" type="primary" @click="handleCreate()"  icon="edit"></el-button>   
  </el-form>

  <div class="content">
  <el-table  :data="tableData"  style="width: 100%" >
    <el-table-column  label=""  width="100">
    <template scope="scope">
      {{scope.$index}}
    </template>
    </el-table-column>
    <el-table-column  prop="style" label=""  width="100"></el-table-column>
    <el-table-column  prop="name" label=""  width="100"></el-table-column>
    <el-table-column  prop="relate" label=""  width="180"></el-table-column>
    <el-table-column  prop="price" label=""  width="130"></el-table-column>
    <el-table-column  prop="department" label=""  width="100"></el-table-column>
    <el-table-column  prop="usestate" label=""  width="100"></el-table-column>
   
    <el-table-column label="">
          <template scope="scope">
            <el-button size="small"  @click="handleEdit(scope.$index, scope.row)"> </el-button>
            <el-button size="small" type="danger"  @click="handleDelete(scope.$index, scope.row)"></el-button> 
          </template>
        </el-table-column>
      </el-table>
  </el-table>    
  
  </div>
  <!--  -->
  <el-dialog title="" v-model="dialogFormVisible" size="tiny" >
    <el-form ref="form" :model="form" label-width="100px">
      <el-form-item label="">
        <el-input v-model="form.style"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.name"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.relate"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.price"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.department"></el-input>
      </el-form-item>
       <el-form-item label="">
      <el-select v-model="form.usestate" placeholder="">
        <el-option label="ture" value="ture"></el-option>
        <el-option label="false" value="false"></el-option>
      </el-select>
      </el-form-item>
      <el-form-item class="center">
        <el-button type="primary" @click="handleSave" :loading="editLoading"></el-button>
        <el-button @click="dialogFormVisible = false"></el-button>
      </el-form-item>
    </el-form>
  </el-dialog>  
  <!--  -->
  <el-dialog title="" v-model="dialogFormVisibleadd" size="tiny">
    <el-form ref="form" :model="form" label-width="100px">
      <el-form-item label="">
        <el-input v-model="form.style"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.name"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.relate"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.price"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.department"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.usestate"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="handleSaveadd" :loading="editLoading"></el-button>
        <el-button @click="dialogFormVisibleadd = false"></el-button>
      </el-form-item>
    </el-form>
  </el-dialog>  
  <!--  -->
  <div class="block">
     <el-pagination
       @size-change="handleSizeChange"
       @current-change="handleCurrentChange"
       :current-page="currentPage1"
       :page-sizes="[10, 20, 30, 40]"
       :page-size="100"
       layout="total, sizes, prev, pager, next, jumper"
       :total="tableData.length">
     </el-pagination>
   </div>
   </div>
   </div>
</template>
<script >

export default {
  name: 'package',
  data () {
    return {
      msg: '>',
      dialogFormVisible:false,
      dialogFormVisibleadd:false,
      editLoading:false,//
      currentPage1: 5,
      activeIndex: '1',
      formInline: {
         user: '',
         region: '',
         state:'',
      },
       form: {
          style: '',
          name: '',
          relate:'',
          price:'',
          department:'',
          usestate:'',
        },
      tableData: [{
                 
                 style: '',
                 name: '',
                 relate:'',
                 price:'1,200',
                 department:'',
                 usestate:'true',
               }, {
                 
                 style: '',
                 name: '',
                 relate:'',
                 price:'200',
                 department:'',
                 usestate:'true',
               }, {
                
                style: '',
                name: 'B',
                relate:'',
                price:'1,200',
                department:'',
                usestate:'true',
               }, {
               
               style: '',
               name: 'B',
               relate:'',
               price:'1,200',
               department:'',
               usestate:'true',
               }]
    }
  },
  methods: {
     onSubmit() {
       this.$message('~');
     },

     handleEdit (index, row) {
       this.dialogFormVisible = true;
       this.form = Object.assign({}, row);
       this.table_index = index;
     },

     handleSave () {
       this.$confirm('', '', {
         confirmButtonText: '',
         cancelButtonText: '',
         cancelButtonClass: 'cancel'
       }).then(() => {
        this.editLoading = true;//
        this.tableData[this.table_index] = this.form;
        this.tableData.splice(this.table_index, 1, this.form);
        this.$message({
           message: "",
           type: 'success'
         });
         this.editLoading = false;
         this.dialogFormVisible = false;
       })
     },

     handleSaveadd () {

       this.$confirm('', '', {
         confirmButtonText: '',
         cancelButtonText: '',
         cancelButtonClass: 'cancel'
       }).then(() => {
        //this.editLoading = true;//
        let vm=this;
        vm.tableData.push(vm.form);
        this.$message({
           message: "",
           type: 'success'
        });
        this.dialogFormVisibleadd = false;
       });
     },

     handleDelete (index, row) {
       this.tableData.splice(index, 1);
       this.$message({
         message: "",
         type: 'success'
       });
     },

     handleCreate(){    
      this.initform();// //
      this.dialogFormVisibleadd = true; 
     },

     //
     initform(){
      this.form={
          style: '',
          name: '',
          relate:'',
          price:'',
          department:'',
          usestate:'',
        }
     },

      handleSizeChange(val) {
      console.log(` ${val} `);
      },

      handleCurrentChange(val) {
      console.log(`: ${val}`);
      },
      handleSelect(key, keyPath) {
      console.log(key, keyPath);
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >
.package{
  padding: 20px;
}

.package .content{
 /* width: 95%;*/
  height: 400px;
  background: white;
 /* margin: 0 auto;*/
}
.package .block{
  text-align: center;
}
.package .center{
  text-align: center;
}


</style>



