<template>
	<div>
		<div class="inOne-advice">
		  <el-input v-model="searchTxt" placeholder="" style="width:25%"></el-input>
		  <el-button type="primary" icon="search"></el-button>
		  <el-button type="primary" icon="edit" @click="handleAdd()"></el-button>
		</div>
		<!--  -->
		<div class="inOne-table">
		  <el-table :data="tableData" border style="width: 100%" @selection-change="handleSelectionChange">
		    <el-table-column type="selection" width="50">
		    </el-table-column>
		    <el-table-column align="center" label='' width="100">
		        <template scope="scope">
		          {{ scope.row.groupIndex}}
		      </template>
		    </el-table-column>
		    <el-table-column align="center" label='()' width="150">
		        <template scope="scope">
		          {{ scope.row.dragName }}
		      </template>
		    </el-table-column>
		    <el-table-column align="center" label='' width="100">
		        <template scope="scope">
		         {{ scope.row.days }}
		      </template>
		    </el-table-column>
		    <el-table-column align="center" label='' width="100">
		        <template scope="scope">
		          {{ scope.row.number }}
		      </template>
		    </el-table-column>
		    <el-table-column align="center" label='()' width="100">
		        <template scope="scope">
		          {{ scope.row.price }}
		      </template>
		    </el-table-column>
		    <el-table-column label="">
		      <template scope="scope">
		      	<el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
		        <el-button size="small" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>		        
		      </template>
		    </el-table-column>
		  </el-table>
		</div>		
		<!---->
		<div>
			<el-dialog title="" :visible.sync="dialogFormVisible" :close-on-click-modal="false">
          		<el-form label-position="left" label-width="90px" style='width: 400px; margin-left:50px;'
          		:model="addform" :rules="addFormRules" ref="addform">
					<el-form-item label="" prop="groupIndex">
            			<el-select v-model="addform.groupIndex" placeholder="">
    						<el-option v-for="item in values" :key="item.value" v-model="item.value">
    						</el-option>
  						</el-select>
            		</el-form-item>
            		<el-form-item label="()" prop="dragName">
              			<el-input v-model="addform.dragName"></el-input>
            		</el-form-item>
            		<el-form-item label="" prop="days">
						<el-input-number :min="1" :max="58" v-model="addform.days"></el-input-number>
					</el-form-item>
			        <el-form-item label="" prop="number">
			            <el-input-number :min="1" :max="200" v-model="addform.number"></el-input-number>
			        </el-form-item>
			        <el-form-item label="()" prop="price">
			            <el-input-number :min="1" v-model="addform.price"></el-input-number>
			        </el-form-item>
          		</el-form>
          		<div slot="footer" class="dialog-footer">
          			<el-button type="primary" @click="handleSubmit()"></el-button>
            		<el-button type="primary" @click="handleReset()"></el-button>
            		<el-button @click="addCancel()"></el-button>
          		</div>
   		    </el-dialog>
   		</div>	
   		<!---->
		<div>
			<el-dialog title="" :visible.sync="dialogEditVisible" :close-on-click-modal="false">
          		<el-form label-position="left" label-width="90px" style='width: 400px; margin-left:50px;' :model="editform" :rules="addFormRules" ref="editform">
					<el-form-item label="" prop="groupIndex">
            			<el-select v-model="editform.groupIndex" placeholder="">
    						<el-option v-for="item in values" :key="item.value" v-model="item.value">
    						</el-option>
  						</el-select>
            		</el-form-item>
            		<el-form-item label="()" prop="dragName">
              			<el-input v-model="editform.dragName"></el-input>
            		</el-form-item>
            		<el-form-item label="" prop="days">
						<el-input-number :min="1" :max="58" v-model="editform.days"></el-input-number>
					</el-form-item>
			        <el-form-item label="" prop="number">
			            <el-input-number :min="1" :max="200" v-model="editform.number"></el-input-number>
			        </el-form-item>
			        <el-form-item label="()" prop="price">
			            <el-input-number :min="1" v-model="editform.price"></el-input-number>
			        </el-form-item>
          		</el-form>
          		<div slot="footer" class="dialog-footer">
          			<el-button type="primary" @click="editSubmit()"></el-button>
            		<el-button @click="editCancel()"></el-button>
          		</div>
   		    </el-dialog>
   		</div>		
		<!-- <div class="inOne-Bottom">
			<el-button type="danger" @click="handleDelAll()"></el-button>
		</div> -->
    </div>
</template>

<style>
	.inOne-Bottom{
		margin-top: 20px;
	}
	.toolbarBottom{
		padding: 0 !important;
	}
</style>

<script>
import Vue from 'vue'
import {api} from '../../global/api'
export default {
	data() {
    	return {
	    	value:'',
	    	input:'',
	        tableData: [],
	        // dialogTableVisible: false,
	        dialogFormVisible: false,
	        dialogEditVisible: false,
	        values:[{
	        	value:'001'
	        },{
	        	value:'002'
	        },{
	        	value:'003'
	        },{
	        	value:'004'
	        },{
	        	value:'005'
	        },{
	        	value:'006'
	        },{
	        	value:'007'
	        }],
	        //
	        addform: {
	            groupIndex:'001',
	            dragName: '',
	            days: null,
	            number: null,
	            price: null
	        },
	        addFormRules: {
	        	dragName:[
	        		{ required: true, message: '()', trigger: 'blur' }
	        	]
	        },
	        editform: {
	            groupIndex:'',
	            dragName: '',
	            days: null,
	            number: null,
	            price: null
	        },
	        editIndex: null,
	        multipleSelection: [],
	        len:null,
			searchTxt: ''
    	}
  	},
	methods:{
	    //
    	getLists(){
    		let that = this
	      	Vue.http.get(api.inquiryData).then(response  => {
	      		console.log("");
	      		console.log(response);
	      			
	      		let formdata = response.data.adviceDatas;
	      		console.log("json",formdata);
	      		that.tableData = formdata;
	      	}, response => {
	      		alert("")
	      	})
    	},
    	//
    	handleDelete(index, row) {
    		let that = this
        	// console.log('row',index,'-----',row);
    		that.$confirm('', '', {}).then(() => {
    			that.$message({
    				type: 'success',
    				message: ''
    			});
    			that.tableData.splice(index,1)
    		}).catch(() => {
    			this.$message({
    			    type: 'info',
    			    message: ''
    			}); 
			});	
    	},
    	//
    	handleSelectionChange(selection){
    		// console.log(selection)   //selection  
    		this.multipleSelection = selection;
    		this.len = this.multipleSelection.length;
    	},
    	//
    	handleAdd(){
    		this.dialogFormVisible = true;
    		//
    		this.addform = {
	            groupIndex:'001',
	            dragName: '',
	            days: null,
	            number: null,
	            price: null
	        }
    	},
    	//
    	handleSubmit(){
    		this.$refs.addform.validate((valid) => {
    			let that = this
    			if(valid){
    				that.$confirm('', '', {}).then(() => {
    					that.$message({
    						type: 'success',
    						message: ''
    					});
    					that.tableData.push(that.addform);
    					that.dialogFormVisible = false;
    					// that.$refs.addform.resetFields();
    				}).catch(() => {
    					 // that.$refs.addform.resetFields();
					});	
    			}
	    	})
    	},
    	//
    	addCancel(){
    		this.dialogFormVisible = false;
    	},
    	//
    	handleReset(){
    		this.$refs.addform.resetFields();
    	},
    	//
    	handleEdit(index, row) {
      		this.dialogEditVisible = true;
      		// this.editform = row;                  // 
      		this.editform = Object.assign({}, row);  //
      		this.editIndex = index;
    	},
    	//
    	editSubmit(){
    		this.$refs.editform.validate((valid) => {
    			let that = this
    			if(valid){
    				that.$confirm('', '', {}).then(() => {
    					that.$message({
    						type: 'success',
    						message: ''
    					});
    					that.tableData[that.editIndex] = that.editform;
						that.dialogEditVisible = false;
    				}).catch(() => {
    		
					});	
    			}
	    	})
    	},
    	//
    	editCancel(){
    		this.dialogEditVisible = false;
    	}
  	},
  	mounted() {
  		this.getLists();
	}
}
</script>


