	    		this.form.mon_selected = mon;
	    		this.form.day_selected = day;
	    		for(let i=0;i<7;i++){
	    			if(mon===this.month1&&day===(this.day1+i)){
	    				// (text ===""),pop
	    				if(this.weeks_content[i].todos[0].text===""){
	    					this.weeks_content[i].todos.pop();
	    				}
		    			
