<template>
  <div class="Vdetails">   

    <div class="content" style="margin-top:15px;text-align:left;">
        <div class="content-left" style="height:100%;width:30%;float:left;">
            <div class="content-left-top" style="height:40%;background-color:#fff;">
                <div class="img-1" style="padding-top:3%; padding-left: 33%;">
                <img src="../../assets/29.png" class="bells" > 
                <p style="padding-left: 6%;"></p>
                </div>
               <table border="0" cellspacing="0" cellpadding="0" style="width:100%;margin-top:6%;">
              <tr style="height:50px;">
              <td>: <span></span></td>
              <td style="border-left:2px solid #F6F6F6;">: <span>30</span></td>
              </tr>
              <tr style="height:50px;">
              <td colspan="2">:<span>13012345678</span></td>
              </tr>
              </table>
            </div>
            <div class="content-left-bottom" style="height:55%;background-color:#fff;margin-top:5%;">
                <table border="0" cellspacing="0" cellpadding="0" style="width:100%;margin-top:6%;text-align: left;">

                <tr style="height:50px;">
              <td>: <span style="margin-left: 45%;"  @click="dialogFormVisible_1 = true"><el-button type="text"><i class="el-icon-document"></i>  </el-button>

              </span></td> 
                 <el-dialog title="" :visible.sync="dialogFormVisible_1">
                        <el-form :model="form"  label-width="80px">

                          <el-form-item label=":"  class="la-span">
                              <span v-model="value1">{{form.name}} </span>
                              <span v-model="value1">{{form.date}} </span>
                              <span v-model="value1">{{form.time}} </span>
                              <span v-model="value1">{{form.doctor}} </span>
                          </el-form-item>
                       
                          <el-form-item label=":">
                              <el-input v-model="form.height"></el-input>
                          </el-form-item>
                          <el-form-item label=":">
                              <el-input v-model="form.weight"></el-input>
                          </el-form-item>
                          <el-form-item label=":">
                              <el-input v-model="form.temper"></el-input>
                          </el-form-item>
                          <el-form-item label=":">
                              <el-input v-model="form.pressure"></el-input>
                          </el-form-item>
                          <el-form-item label=":">
                              <el-input v-model="form.rate"></el-input>
                          </el-form-item>


                        </el-form>
                        <div slot="footer" class="dialog-footer" style="text-align:center;">
                          <el-button @click="dialogFormVisible_1 = false"> </el-button>
                          <el-button type="success" @click="submit()"></el-button>
                      </div>
                  </el-dialog>           
              </tr>

              <tr style="height:50px;" v-model="formData">
              <td>: <span v-model="formData.height">{{ formData.height }}</span></td>            
              </tr>

                <tr style="height:50px;">
              <td>: <span v-model="formData.weight">{{ formData.weight }}</span></td>            
              </tr>
               
                <tr style="height:50px;">
              <td>: <span v-model="formData.temper">{{ formData.temper }}</span></td>            
              </tr>

                <tr style="height:50px;">
              <td>: <span v-model="formData.pressure">{{ formData.pressure }}</span></td>            
              </tr>

                <tr style="height:50px;">
              <td>: <span v-model="formData.rate">{{ formData.rate }}</span></td>            
              </tr>

              </table>
            </div>
        </div>
        <div class="content-right" style="height:100%;width:60%;margin-left:30px;float:left;">
            <div class="content-right-top" style="height:43%;background-color:#fff;">  
                <table border="0" cellspacing="0" cellpadding="0" style="width:100%;text-align: left;" >

                  <tr style="height:50px;" v-model="formData1">
                   <td style="font-weight: bold;"></td>            
                </tr>

                <tr style="height:50px;">
                   <td>: 
                        <span v-model="form.value_res">
                          {{form.value_res}}
                        </span>
                      </td>            
                </tr>

                  <tr style="height:50px;">
                     <td>: <span v-model="form.time">{{form.time}}</span></td>            
                </tr>
                 
                  <tr style="height:50px;">
                     <td>: <span v-model="form.doc_subject">{{form.doc_subject}}</span></td>            
                </tr>



                  <tr style="height:50px;">
                     <td>: <span v-model="formData.rate">{{form.rate}}</span><span style="margin-left: 80%;"><el-button type="text"><i class="el-icon-document"></i>  </el-button></span></td>            
                </tr>

              </table>
            </div>
            <div class="content-right-bottom" style="height:37%;background-color:#fff;margin-top:3%;">
                
                <table border="0" cellspacing="0" cellpadding="0" style="width:100%;text-align: left;">

                <tr style="height:50px;" v-model="formData2">
              <td style="font-weight: bold;"></td>            
              </tr>

              <tr style="height:50px;">
              <td>: 
                    <span v-model="form.timereservate">{{form.timeDate}}</span>
                    <span v-model="form.timereservate">{{form.timeTime}}</span>
                </td>            
              </tr>

                <tr style="height:50px;">
              <td>: <span v-model="form.doctor">{{form.doctor}}</span></td>            
              </tr>
               
                <tr style="height:50px;">
              <td>: <span v-model="form.doc_subject">{{form.doc_subject}}</span></td>            
              </tr>
              </table>
            </div>
            <div class="content-right-bottom2" style="margin-top: 3%">
                <span class="wrapper">
                  <el-button type="success" @click="editorDialogForm"></el-button>
              
                      <el-dialog title="" :visible.sync="dialogFormVisible">
                        <el-form :model="form2">

                          <el-form-item label=":"  class="la-span">
                              <span v-model="form.name">{{form.name}} </span>
                              <span v-model="form.timeDate">
                                  {{ form.timeDate }}
                              </span>
                              <span v-model="form.time">{{form.timeTime}} </span>
                              <span v-model="form.doctor">{{form.doctor}} </span>
                          </el-form-item>
                          <el-form-item label="" >

                              <el-date-picker
                                v-model="form2.timeDate"
                                type="date"
                                placeholder=""
                                :picker-options="pickerOptions0">                              
                              </el-date-picker>

                              <el-time-select
                                v-model="form2.timeTime"
                                :picker-options="{
                                  start: '08:30',
                                  step: '00:15',
                                  end: '18:30'
                                }"
                                placeholder="">
                              </el-time-select>
                            

                          </el-form-item>
                        </el-form>
                        <div slot="footer" class="dialog-footer" style="text-align:center;">
                          <el-button @click="dialogFormVisible = false"> </el-button>
                          <el-button type="success" @click="submit1()"></el-button>
                        </div>
                      </el-dialog>

                  <el-button type="success" @click="edit_dialogFormVisible = true"> </el-button>
                      <el-dialog title="" :visible.sync="edit_dialogFormVisible">
                        <el-form :model="form">

                          <el-form-item label=":"  class="la-span">
                              <span v-model="form.name">{{form.name}} </span>
                              <span v-model="form.timeDate">
                                  {{ form.timeDate }}
                              </span>
                              <span v-model="form.time">{{form.timeTime}} </span>
                              <span v-model="form.doctor">{{form.doctor}} </span>
                          </el-form-item>

                          <el-form-item label="" >
                              <el-select v-model="form.doc_subject" placeholder="">
                                  <el-option label="" value=""></el-option>
                                  <el-option label="" value=""></el-option>
                                  <el-option label="" value=""></el-option>
                              </el-select>
                              <el-select v-model="form.doctor" placeholder="">
                                  <el-option label="" value=""></el-option>
                                  <el-option label="" value=""></el-option>
                                  <el-option label="" value=""></el-option>
                              </el-select>
                          </el-form-item>
                      
                          <el-form-item label="" >
                              <el-date-picker
                                v-model="form.timeDate"
                                type="date"
                                placeholder=""
                                :picker-options="pickerOptions0">                              
                              </el-date-picker>

                              <el-time-select
                                v-model="form.timeTime"
                                :picker-options="{
                                start: '08:30',
                                step: '00:15',
                                end: '18:30'
                                }"
                                placeholder="">
                              </el-time-select>

                          </el-form-item>



                        </el-form>
                        <div slot="footer" class="dialog-footer" style="text-align:center;">
                          <el-button @click="edit_dialogFormVisible = false"> </el-button>
                          <el-button type="success" @click="submit2()"></el-button>
                        </div>
                      </el-dialog>

                 
                  <el-button ></el-button>
                 
                </span>
            </div>

        </div>
    </div>
  </div>
</template>

<script>
// import Home from '@/view/home';
export default {
  name: 'Vdetails',
  data: function () {
    return {
       
        // dialogTableVisible: false,
        dialogFormVisible_1:false,
        dialogFormVisible: false,
        edit_dialogFormVisible:false,

        formData: {
          
          height:'-',
          weight:'-',
          temper:'-',
          pressure:'-',
          rate:'-',
        },
        formData1:{
          value_res:'-',
          time:'-',
          doc_subject:'-',
        },
        formData2:{
      
          time:'-',
          doctor:'-',
          doc_subject:'-',
        },
        form: {
          
          name: '',
          date:'2017-07-10',
          time:'9:00',
          doctor:'',
          doc_subject:'',
          height:'',
          weight:'',
          temper:'',
          pressure:'',
          rate:'',
          value_res:'2017-8-9',
          timeDate:'2017-8-10',
          timeTime:'10:00'

        },
        form2:{
          timeDate:'',
          timeTime:''
        },
        // formLabelWidth: '200px',
        pickerOptions0: {
          disabledDate(time) {
            return time.getTime() < Date.now() - 8.64e7;
          }
        },
        day:'',
        value1:''
        // JSON.stringify(value_res)
        // this.form.date = JSON.parse(JSON.stringify(this.value_res))         
    }
  },
    methods:{
      editorDialogForm:function(){
        this.dialogFormVisible = true;
        this.form2={
          timeDate:'',
          timeTime:''
        };
      },
      

      handleIconClick:function(ev) {
      console.log(ev);
      },
      
      submit:function(){
            // this.formData= this.form()
            this.formData = JSON.parse(JSON.stringify(this.form));
            console.log("", JSON.stringify(this.form) );
            this.dialogFormVisible_1 = false

        },
         submit1:function(){
            // this.formData= this.form()
            this.formData1 = JSON.parse(JSON.stringify(this.form));
            console.log("", JSON.stringify(this.form) );
            this.dialogFormVisible = false;
            this.form.value_res=new Date().getFullYear()+'-'+(new Date().getMonth()+1)+'-'+new Date().getDate();
            this.form.time=new Date().getHours()+':'+new Date().getMinutes();
            this.form.timeDate=new Date(this.form2.timeDate).getFullYear()+'-'+(new Date(this.form2.timeDate).getMonth()+1)+'-'+new Date(this.form2.timeDate).getDate();
            this.form.timeTime=this.form2.timeTime;
        },
         submit2:function(){
            // this.formData= this.form()
            this.formData2 = JSON.parse(JSON.stringify(this.form));
            console.log("", JSON.stringify(this.form) );
            this.edit_dialogFormVisible = false;
            this.form.timeDate=new Date(this.form.timeDate).getFullYear()+'-'+(new Date(this.form.timeDate).getMonth()+1)+'-'+new Date(this.form.timeDate).getDate();
            this.form.timeTime=this.form.timeTime;
        }




        
    }

}
</script>

<style>
*{
  margin: 0;
  padding: 0;
}
body{
background-color: #F6F6F6;
}
.Vdetails .img-1{
   /* max-width: 200px;
    max-height: 200px;*/
    border-radius: 50%;
}
.Vdetails   td{   
   border-top:2px solid #F6F6F6;  
  
}  
.Vdetails  .content-left-top td{
 padding-left: 3%;
}
.Vdetails  .content-left-bottom td{
 padding-left: 3%;
}
.Vdetails  .content-right-top td{
 padding-left: 3%;
}
.Vdetails  .content-right-bottom td{
 padding-left: 3%;
}
.Vdetails .la-span span{
margin-left: 3%;
}
</style>



