<template>
  <div class="account" >
    
    <br>
    <el-form :inline="true" :model="formInline" class="demo-form-inline">

      <el-form-item label="">
        <el-input v-model="formInline.account" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-select v-model="formInline.region" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="vip" value="vip"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="formInline.user" placeholder=""></el-input>
      </el-form-item>

      <el-form-item>
        <el-button type="success" class="el-icon-search"@click="onSubmit"></el-button>
      </el-form-item> 

    </el-form>

    <!--  -->
    <div class="content">
      <el-table  :data="tableData"  account_type="width: 100%" >
        <el-table-column  prop="account"label=""  width="110"></el-table-column>
       
        <el-table-column  prop="account_type" label=""  width="150"></el-table-column>
        <el-table-column  prop="username" label=""  width="110"></el-table-column>
        <el-table-column  prop="share" label=""  width="130"></el-table-column>
        <el-table-column  prop="money" label=""  width="150"></el-table-column>
        <el-table-column  prop="deadline" label=""  width="130"></el-table-column>
        <el-table-column label="">
              <template scope="scope">
                <el-button size="small" type="success" @click="accountPay(scope.$index, scope.row)"> </el-button>
                <el-button size="small" type="success" @click="account_typeChange(scope.$index, scope.row)"> </el-button>
                <el-button size="small" type="success" @click="familyShare(scope.$index, scope.row)"> </el-button>
                <el-button size="small" type="danger"  @click="handleDelete(scope.$index, scope.row)"></el-button> 
              </template>
        </el-table-column>
      </el-table>
    </div>

    <!--  -->
    <el-dialog title="" v-model="dialogFormVisible_moneypay" size="tiny" >
      <el-form ref="form" :model="form" label-width="100px">

        <i class="fa fa-user-circle-o fa-2x" aria-hidden="true"></i> <span>{{form.username}}</span>
        <br><br><br>

        <el-form-item label="" class="lleft">
          <el-input v-model="form.money"></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-select v-model="form.account_type" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="">
        <el-select v-model="value" placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item class="center">
          <el-button type="primary" @click="handleSave" :loading="editLoading"></el-button>
          <el-button @click="dialogFormVisible_moneypay = false"></el-button>
        </el-form-item>

      </el-form>
    </el-dialog>  

    <!--  -->
    <el-dialog title="" v-model="dialogFormVisible_moneypayagain" size="tiny" >
      <el-form ref="form" :model="form" label-width="100px">

        <div class="left"><i class="fa fa-id-card fa-2x" aria-hidden="true"></i><span class="space">{{form.account}}</span></div>
        <div  class="right"><i class="fa fa-usd  fa-2x" aria-hidden="true"></i><span class="content_size">{{form.money}}</span></div>  <br><br><br>
        

        <el-form-item label=""><span>{{form.username}}</span> </el-form-item>

        <el-form-item label="" class="lleft">
          <el-input v-model="paymoney"></el-input>
        </el-form-item>

        <el-form-item label="">
        <el-select v-model="value" placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>

      <el-form-item class="center">
      <template scope="scope">
        <el-button type="primary" @click="handleSave_moneypayagain(scope.$index, scope.row)" :loading="editLoading"></el-button>
        <el-button @click="dialogFormVisible_moneypayagain = false"></el-button>
      </template>
      </el-form-item>

      </el-form>
    </el-dialog>

    <!--  -->
    <el-dialog title="" v-model="dialogFormVisible_changeaccount_type" size="tiny" >
      <el-form ref="form" :model="form" label-width="120px">
        <div class="left"><i class="fa fa-id-card fa-2x" aria-hidden="true"></i><span class="space">{{form.account}}</span></div>
        <div  class="right"><i class="fa fa-usd  fa-2x" aria-hidden="true"></i><span class="content_size">{{form.money}}</span></div>  <br><br><br>
        
        <el-form-item label=""><span>{{form.username}}</span> </el-form-item>

        <el-form-item label=""><span>{{form.account_type}}</span> </el-form-item>

         <el-form-item label="">
          <el-select v-model="changeaccount_type" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
          </el-form-item>

        <el-form-item class="center">
        <template scope="scope">
          <el-button type="primary" @click="handleSave_changeaccount_type()" :loading="editLoading"></el-button>
          <el-button @click="dialogFormVisible_changeaccount_type = false"></el-button>
        </template>
        </el-form-item>

      </el-form>
    </el-dialog>   

    <!--  -->
    <el-dialog title="" v-model="dialogFormVisible_familyShare" size="tiny" >
      <el-form ref="form" :model="form" label-width="120px">
      <div class="left"><i class="fa fa-id-card fa-2x" aria-hidden="true"></i><span class="space">{{form.account}}</span></div>
      <div  class="right"><i class="fa fa-usd  fa-2x" aria-hidden="true"></i><span class="content_size">{{form.money}}</span></div> 
      <br><br><br>

      <el-form-item label=""><span>{{form.username}}</span> </el-form-item>

      <el-form-item label=""><span>{{form.share}}</span></el-form-item>

      <el-form-item label="">
        <el-input v-model="changefamilyShare" placeholder=""></el-input>
      </el-form-item>

      <el-form-item class="center">
      <template scope="scope">
        <el-button type="primary" @click="handleSave_familyShare()" :loading="editLoading"></el-button>
        <el-button @click="dialogFormVisible_familyShare = false"></el-button>
      </template>
      </el-form-item>

      </el-form>
    </el-dialog>   

    <!--  -->
    <div class="block">
       <el-pagination
         @size-change="handleSizeChange"
         @current-change="handleCurrentChange"
         :current-page="currentPage1"
         :page-sizes="[10, 20, 30, 40]"
         :page-size="100"
         layout="total, sizes, prev, pager, next, jumper"
         :total="tableData.length">
       </el-pagination>
    </div>
    
  </div>
</template>
<script >
import {api} from '../../global/api';
export default {
  username: 'account',
  data () {
    return {
      
      dialogFormVisible_moneypay:false,
      dialogFormVisible_moneypayagain:false,
      dialogFormVisible_changeaccount_type:false,
      dialogFormVisible_familyShare:false,
      editLoading:false,//
      currentPage1: 5,
      activeIndex: '1',
      value: '',
      paymoney:'',
      changeaccount_type:'',
      changefamilyShare:'',
      formInline: {
         user: '',
         region: '',
         account:'',
      },
     form: {
        account:'',
        account_type: '',
        username: '',
        share:'',
        money:'',
        deadline:'',
        type:'',

      },
      options: [{
        value: '1',
        label: ''
      }, {
        value: '2',
        label: ''
      }],
      tableData:[
                  {
                      account:'',
                      account_type: '',
                      username: '',
                      share:'',
                      money:'',
                      deadline:'',
                  }
                ]
      }
    },
    //
    mounted:function(){
      this.$http.get(api.style).then(function(response){
        this.tableData=response.data.tableData;
      });
    },
    methods: {
        onSubmit() {
         this.$message('~');
        },
       //
        accountPay (index, row) {
        let money=this.tableData[index].money;
        if(money===null){
         this.dialogFormVisible_moneypay = true;
        }
        else{
         this.paymoney=null;
         this.dialogFormVisible_moneypayagain = true;
        }
         this.form = Object.assign({}, row);
         this.table_index = index;
        },
        //
        account_typeChange(index, row){
         this.changeaccount_type='';
         this.dialogFormVisible_changeaccount_type = true;
         this.form = Object.assign({}, row);
         this.table_index = index;
        },
        //
        familyShare(index, row){
         this.changefamilyShare='';
         this.dialogFormVisible_familyShare = true;
         this.form = Object.assign({}, row);
         this.table_index = index;
        },
        //
        handleSave () {
         this.$confirm('', '', {
           confirmButtonText: '',
           cancelButtonText: '',
           cancelButtonClass: 'cancel'
         }).then(() => {
          this.editLoading = true;//
          this.tableData[this.table_index] = this.form;
          this.tableData.splice(this.table_index, 1, this.form);
          this.$message({
             message: "",
             type: 'success'
           });
           this.editLoading = false;
           this.dialogFormVisible_moneypay = false;
         })
        },
        //
        handleSave_moneypayagain (index,row) {
         this.$confirm('', '', {
           confirmButtonText: '',
           cancelButtonText: '',
           cancelButtonClass: 'cancel'
         }).then(() => {
          if(this.paymoney){
            this.form.money=parseInt(this.paymoney)+this.form.money;
          }
          this.tableData[this.table_index] = this.form;
          this.tableData.splice(this.table_index, 1, this.form);
          this.$message({
             message: "",
             type: 'success'
           });
           this.editLoading = false;
           this.dialogFormVisible_moneypayagain = false;
          })
        },
        //
        handleSave_changeaccount_type () {
         this.$confirm('', '', {
           confirmButtonText: '',
           cancelButtonText: '',
           cancelButtonClass: 'cancel'
         }).then(() => {
          this.editLoading = true;//
          this.form.account_type=this.changeaccount_type;
          this.tableData[this.table_index] = this.form;
          this.tableData.splice(this.table_index, 1, this.form);
          this.$message({
             message: "",
             type: 'success'
           });
           this.editLoading = false;
           this.dialogFormVisible_changeaccount_type = false;
         })
        },
         //
        handleSave_familyShare() {
         this.$confirm('', '', {
           confirmButtonText: '',
           cancelButtonText: '',
           cancelButtonClass: 'cancel'
         }).then(() => {
          this.editLoading = true;//
          this.form.share=(this.changefamilyShare+','+this.form.share);
          this.tableData[this.table_index] = this.form;
          this.tableData.splice(this.table_index, 1, this.form);
          this.$message({
             message: "",
             type: 'success'
           });
           this.editLoading = false;
           this.dialogFormVisible_familyShare = false;
         })
        },
       //
        handleDelete (index, row) {
         this.tableData.splice(index, 1);
         this.$message({
           message: "",
           type: 'success'
         });
        },
        handleSizeChange(val) {
          console.log(` ${val} `);
        },
        handleCurrentChange(val) {
          console.log(`: ${val}`);
        }
    }
}
</script>
<style type="text/css">>
  .account{
    padding: 20px;
  }
  .account .content{
    height: 400px;
   background: white;
  }
  .account .block{
    text-align: center;
  }
  .account .center{
    text-align: center;
  }
  .account .left{
    width: 50%;
    float: left;
    text-align: center;
  }
  .account .right{
     width: 50%;
    float: right;
    text-align: center;
  }
  .account .lleft{
    text-align: left;
  }
  .account .el-form-item__content {
    font-size: 23px;
  }
  .account .content_size{
    font-size: 20px;
  }
  .el-dialog__header{
    background: #f6f6f6;
    padding-bottom: 10px;
  }
</style>



